  --fade_white: rgb(191, 182, 216);
  --dark_blue: #101d30;
  --fade_dark_blue: rgba(16, 29, 48, 0.9);
  --black_title: #2b3954;
  --black_text: #425466;
  --dark_purple: rgb(41, 41, 85);
  --purple: rgb(52, 52, 102);
